.intro {
  position: relative;
  width: 100%;
  height: 100vh;
  z-index: 1;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 990px) {
  .intro {
    margin-bottom: 140px;
  }
}
.intro:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.5;
  z-index: 2;
}
.intro__inner {
  position: absolute;
  z-index: 3;
  transform: translateY(-50%);
  top: 50%;
}
@media (max-width: var(--container-width)) {
  .intro__inner {
    margin-right: 42px;
  }
}
@media (max-width: 990px) {
  .intro__inner {
    margin-right: 20px;
  }
}
.intro__head {
  bottom: 60vh;
}
@media (max-width: 990px) {
  .intro__head {
    position: absolute;
  }
}
.intro__title {
  font-family: "Orbitron", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 112px;
  line-height: 100%;
  text-transform: uppercase;
  color: var(--secondary-color);
}
@media (max-width: 1330px) {
  .intro__title {
    font-size: 70px;
  }
}
@media (max-width: 990px) {
  .intro__title {
    font-size: 40px;
    line-height: 140%;
  }
}
.intro__subtitle {
  font-weight: 700;
  font-size: 46px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: var(--text-color-light);
  margin-bottom: 30px;
}
@media (max-width: 1330px) {
  .intro__subtitle {
    font-size: 28px;
  }
}
@media (max-width: 990px) {
  .intro__subtitle {
    font-size: 16px;
  }
}
.intro__content {
  background: url("../assets/img/info-bg.png") center no-repeat;
  width: 650px;
  height: 254px;
  display: flex;
}
@media (max-width: 1330px) {
  .intro__content {
    background: url("../assets/img/info-bg-1330.png") center no-repeat;
    width: 421px;
    height: 187px;
  }
}
@media (max-width: 990px) {
  .intro__content {
    width: 100%;
    height: 100%;
    background: var(--primary-color);
    margin-top: 100vh;
    border-bottom: 5px solid var(--secondary-color);
  }
}
@media (max-width: 990px) {
  .intro__content:after {
    content: "";
    position: absolute;
    bottom: -10px;
    left: 0;
    right: 0;
    width: 55%;
    height: 10px;
    background-color: white;
    clip-path: polygon(0 0, 100% 0, 96% 100%, 0 100%);
  }
  .intro__content:before {
    content: "";
    position: absolute;
    z-index: 1;
    bottom: -5px;
    right: 0;
    width: 55%;
    height: 5px;
    background-color: var(--primary-color);
    clip-path: polygon(2% 0, 100% 0, 100% 100%, 0 100%);
  }
}
.intro__info {
  padding: 24px 24px 27px 35px;
}
@media (max-width: 1330px) {
  .intro__info {
    padding: 16px 22px;
  }
}
@media (max-width: 990px) {
  .intro__info {
    padding: 17px 20px;
  }
}
.intro__info p {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: var(--text-color-light);
  margin-bottom: 5px;
  width: 500px;
}
@media (max-width: 1330px) {
  .intro__info p {
    font-size: 12px;
    width: 326px;
  }
}
@media (max-width: 990px) {
  .intro__info p {
    font-size: 11px;
    line-height: 140%;
    width: 100%;
  }
}
.intro__info a {
  font-weight: 700;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: var(--text-color-light);
  position: relative;
}
@media (max-width: 1330px) {
  .intro__info a {
    font-size: 12px;
  }
}
.intro__info a:after {
  content: "";
  position: absolute;
  background: url("../assets/img/arrow-read-more-light.svg");
  width: 24.46px;
  height: 16px;
  top: 4px;
  right: -35px;
}

.catalog {
  margin-bottom: 60px;
}
.catalog__materials {
  margin: 50px 0 0 0;
}
.catalog__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 -10px;
}
.catalog__item {
  position: relative;
  padding: 0 10px;
  margin-bottom: 60px;
}
@media (max-width: 1330px) {
  .catalog__item {
    margin-bottom: 45px;
  }
}
.catalog__text {
  padding-top: 10px;
  width: 100%;
  height: 55px;
  font-weight: 900;
  font-size: 20px;
  line-height: 120%;
  display: flex;
  align-items: flex-start;
  text-align: center;
  justify-content: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--text-color-light);
  position: absolute;
  left: 50%;
  bottom: 45px;
  transform: translate(-50%, 0);
}
@media (max-width: 1330px) {
  .catalog__text {
    font-size: 16px;
    line-height: 130%;
  }
}
@media (max-width: 767px) {
  .catalog__text {
    height: 45px;
  }
}
.catalog__text:after {
  content: "";
  position: absolute;
  width: 40px;
  height: 3px;
  background-color: var(--secondary-color);
  bottom: -13px;
  left: 50%;
  transform: translate(-50%, 0);
}
@media (max-width: 1330px) {
  .catalog__text:after {
    height: 1px;
  }
}
.catalog .catalog__figure {
  position: relative;
  z-index: 1;
  width: 190px;
  height: 233px;
  background-color: var(--secondary-color);
  clip-path: var(--figureClipPath);
}
@media (max-width: 1330px) {
  .catalog .catalog__figure {
    width: 140px;
    height: 203px;
    clip-path: polygon(50% 0%, 100% 20%, 110% 75%, 50% 100%, 0% 80%, 0% 20%);
  }
}
@media (max-width: 767px) {
  .catalog .catalog__figure {
    width: 130px;
    height: 154px;
    clip-path: polygon(50% 0%, 100% 20%, 110% 75%, 50% 100%, 0% 80%, 0% 20%);
  }
}
.catalog .box {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 184px;
  height: 227px;
  background-color: var(--primary-color);
  clip-path: var(--figureClipPath);
}
@media (max-width: 1330px) {
  .catalog .box {
    width: 136px;
    height: 199px;
    clip-path: polygon(50% 0%, 100% 20%, 110% 75%, 50% 100%, 0% 80%, 0% 20%);
  }
}
@media (max-width: 767px) {
  .catalog .box {
    width: 126px;
    height: 150px;
    clip-path: polygon(50% 0%, 100% 20%, 110% 75%, 50% 100%, 0% 80%, 0% 20%);
  }
}
.catalog .catalog__figure-material {
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -100%);
  z-index: 3;
}
@media (max-width: 1330px) {
  .catalog .catalog__figure-material {
    top: 45%;
  }
}
.catalog .catalog__image-border-1 {
  position: relative;
  z-index: 4;
  width: 140px;
  height: 162px;
  background-color: var(--secondary-color);
  clip-path: var(--figureClipPath);
}
@media (max-width: 1330px) {
  .catalog .catalog__image-border-1 {
    width: 102px;
    height: 118px;
  }
}
@media (max-width: 767px) {
  .catalog .catalog__image-border-1 {
    width: 80px;
    height: 93px;
  }
}
.catalog .catalog__image-border-2 {
  position: absolute;
  z-index: 5;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 134px;
  height: 156px;
  background-color: white;
  clip-path: var(--figureClipPath);
}
@media (max-width: 1330px) {
  .catalog .catalog__image-border-2 {
    width: 98px;
    height: 114px;
  }
}
@media (max-width: 767px) {
  .catalog .catalog__image-border-2 {
    width: 76px;
    height: 89px;
  }
}
.catalog .box-image {
  position: absolute;
  z-index: 6;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 128px;
  height: 150px;
  background-color: var(--primary-color);
  clip-path: var(--figureClipPath);
}
@media (max-width: 1330px) {
  .catalog .box-image {
    width: 94px;
    height: 110px;
  }
}
@media (max-width: 767px) {
  .catalog .box-image {
    width: 72px;
    height: 85px;
  }
}
.catalog .box-image img {
  width: 128px;
  height: 150px;
  object-fit: cover;
}
@media (max-width: 1330px) {
  .catalog .box-image img {
    width: 94px;
    height: 110px;
  }
}
@media (max-width: 767px) {
  .catalog .box-image img {
    width: 72px;
    height: 85px;
  }
}
.catalog__button {
  display: flex;
  justify-content: center;
}

.benefits {
  background-color: var(--primary-color);
  padding: 0 0 60px 0;
}
@media (max-width: 1330px) {
  .benefits {
    padding-bottom: 40px;
  }
}
.benefits__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 -15px;
}
@media (max-width: 1330px) {
  .benefits__inner {
    margin: 0 -10px -20px -10px;
  }
}
.benefits__item {
  position: relative;
  padding: 0 15px;
  height: 334px;
}
@media (max-width: 1330px) {
  .benefits__item {
    padding: 0 10px;
    height: 241px;
    margin-bottom: 20px;
  }
}
.benefits__figure {
  position: relative;
  z-index: 1;
  width: 190px;
  height: 220px;
  background-color: #3b4560;
  clip-path: var(--figureClipPath);
}
@media (max-width: 1330px) {
  .benefits__figure {
    width: 140px;
    height: 162px;
  }
}
@media (max-width: 767px) {
  .benefits__figure {
    width: 130px;
    height: 149px;
  }
}
.benefits__info {
  position: absolute;
  z-index: 2;
  width: 170px;
  height: 194px;
  top: 140px;
  left: 50%;
  transform: translate(-50%, 0);
  background-color: var(--secondary-color);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 1330px) {
  .benefits__info {
    width: 130px;
    height: 126px;
    top: 115px;
  }
}
@media (max-width: 767px) {
  .benefits__info {
    width: 120px;
    height: 118px;
    top: 105px;
  }
}
.benefits__text {
  height: 165px;
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  display: flex;
  align-items: flex-end;
  text-align: center;
  letter-spacing: 0.02em;
  color: var(--text-color-light);
}
@media (max-width: 1330px) {
  .benefits__text {
    height: 106px;
    font-size: 12px;
    line-height: 130%;
  }
}
.benefits__text span {
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 10px;
}
@media (max-width: 1330px) {
  .benefits__text span {
    height: 65px;
  }
}
.benefits__number {
  height: 30px;
  font-weight: 900;
  font-size: 20px;
  line-height: 140%;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  background-color: #fff;
  color: var(--secondary-color);
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 1330px) {
  .benefits__number {
    height: 20px;
    font-size: 14px;
    line-height: 100%;
  }
}
.benefits__image-border {
  position: absolute;
  left: 50%;
  top: 10px;
  z-index: 3;
  transform: translate(-50%, 0);
  width: 170px;
  height: 198px;
  background-color: #fff;
  clip-path: var(--figureClipPath);
}
@media (max-width: 1330px) {
  .benefits__image-border {
    width: 130px;
    height: 151px;
    top: 5px;
  }
}
@media (max-width: 767px) {
  .benefits__image-border {
    width: 120px;
    height: 139px;
  }
}
.benefits__image {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 4;
  transform: translate(-50%, -50%);
  width: 164px;
  height: 192px;
  clip-path: var(--figureClipPath);
}
@media (max-width: 1330px) {
  .benefits__image {
    width: 128px;
    height: 149px;
  }
}
@media (max-width: 767px) {
  .benefits__image {
    width: 116px;
    height: 135px;
  }
}
.benefits__image img {
  width: 164px;
  height: 192px;
  object-fit: cover;
}
@media (max-width: 1330px) {
  .benefits__image img {
    width: 128px;
    height: 149px;
  }
}
@media (max-width: 767px) {
  .benefits__image img {
    width: 116px;
    height: 135px;
  }
}

.news {
  background-color: var(--secondary-color);
  padding-bottom: 60px;
}
.news__filter {
  margin-bottom: 50px;
  display: flex;
}
.news__btn {
  background-color: transparent;
  border: none;
  width: 220px;
  height: 50px;
  padding: 0;
  margin-right: 2px;
  color: var(--text-color-light);
  font-weight: 700;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.02em;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
}
.news__btn:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: var(--light-color);
  opacity: 0.5;
}

.news__tabs .tabs__nav-btn--active {
  z-index: 1;
  color: var(--text-color-dark);
}
.news__tabs .tabs__nav-btn--active:after {
  opacity: 1;
  z-index: -1;
}
.news__tabs .tabs__panel {
  display: none;
}
.news__tabs .tabs__panel--active {
  display: block;
}